import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPalette, faBoxOpen, faTruck, faShieldAlt, faQuestionCircle, faRulerCombined, faHeadset, faUndo, faCreditCard, faShippingFast } from '@fortawesome/free-solid-svg-icons';

const InfoFaqPage = () => {
    const getColorStyle = (colorName) => {
        const colors = {
            "Biały": "#F5F5F5", "Zielony": "#4CAF50", "Żółty": "#FFEB3B", "Czarny": "#424242",
            "Szary": "#9E9E9E", "Jasny brąz": "#C8AD7F", "Niebieski": "#3F51B5", "Czerwony": "#C62828",
            "Śliski Biały": "#f8f8f8", "Śliski Czarny": "#1a1a1a"
        };
        return colors[colorName] || '#ccc';
    };

    const faqData = [
        {
            icon: faShippingFast,
            question: "Ile trwa realizacja zamówienia?",
            answer: "Standardowo zamówienia realizujemy w ciągu 2–4 dni roboczych. Czas ten może się nieznacznie wydłużyć w przypadku dużej liczby zamówień lub skomplikowanych projektów. Zawsze staramy się działać tak szybko, jak to możliwe!"
        },
        {
            icon: faRulerCombined,
            question: "Czy mogę zamówić wydruk w niestandardowym rozmiarze lub kolorze?",
            answer: "Oczywiście! Jesteśmy otwarci na indywidualne projekty. Skontaktuj się z nami przez formularz lub mailowo, opisz swoje potrzeby, a my przygotujemy dla Ciebie spersonalizowaną ofertę."
        },
        {
            icon: faBoxOpen,
            question: "Z jakiego materiału wykonane są wydruki?",
            answer: "Nasze wydruki standardowo tworzymy z wysokiej jakości, biodegradowalnego materiału PLA (polilaktyd). Jest on trwały, estetyczny i przyjazny dla środowiska. Na życzenie możemy również wykorzystać inne materiały, takie jak PETG czy ABS."
        },
        {
            icon: faShieldAlt,
            question: "Czy wydruki 3D są wytrzymałe?",
            answer: "Tak, wydruki z materiału PLA są wystarczająco wytrzymałe do codziennego użytku, dekoracji czy prototypowania. Należy jednak pamiętać, że nie jest to materiał przeznaczony do zastosowań wymagających ekstremalnej odporności mechanicznej czy termicznej."
        },
        {
            icon: faCreditCard,
            question: "Jakie są dostępne formy płatności?",
            answer: "Akceptujemy płatności przelewem tradycyjnym oraz płatności online (BLIK, szybki przelew). Przy odbiorze osobistym możliwa jest również płatność gotówką."
        },
        {
            icon: faTruck,
            question: "Jakie są opcje i koszty dostawy?",
            answer: "Oferujemy wysyłkę za pośrednictwem InPost (paczkomaty i kurier). Koszt dostawy jest stały i wynosi 15 zł. Możliwy jest również bezpłatny odbiór osobisty na terenie Rzeszowa po wcześniejszym umówieniu."
        },
        {
            icon: faUndo,
            question: "Czy mogę zwrócić zamówiony produkt?",
            answer: "Tak, masz 7 dni na zwrot produktu od momentu jego otrzymania, bez podawania przyczyny. Pamiętaj, że zwracany produkt nie może nosić śladów użytkowania. Zwroty nie obejmują zamówień personalizowanych."
        },
        {
            icon: faHeadset,
            question: "Jak mogę się z wami skontaktować?",
            answer: 'Najszybciej skontaktujesz się z nami mailowo pod adresem <a href="mailto:rex3d.kontakt@gmail.com">rex3d.kontakt@gmail.com</a> lub przez formularz kontaktowy na naszej stronie. Odpowiadamy na wszystkie wiadomości w ciągu 24 godzin.'
        }
    ];

    return (
        <div className="container mt-5 pt-5">
            <div className="text-center mb-5">
                <h1 className="display-4">Informacje i Pomoc</h1>
                <p className="lead text-muted">Wszystko, co musisz wiedzieć o naszych produktach i procesie zamówień.</p>
            </div>

            <div className="row">
                <div className="col-lg-6 mb-4">
                    <h3 className="mb-4"><FontAwesomeIcon icon={faPalette} className="me-2 text-primary"/> Kolory i Dostępność</h3>
                    <div className="table-responsive">
                        <table className="table table-hover" aria-label="Tabela dostępnych kolorów i wykończeń">
                            <thead>
                                <tr>
                                    <th scope="col">Kolor</th>
                                    <th scope="col">Wykończenie</th>
                                    <th scope="col">Próbka</th>
                                </tr>
                            </thead>
                            <tbody>
                                {["Biały", "Zielony", "Żółty", "Szary", "Niebieski", "Jasny brąz", "Czarny", "Czerwony"].map(color => (
                                    <tr key={color}>
                                        <td>{color}</td>
                                        <td>Matowy</td>
                                        <td><span className="color-dot" style={{ backgroundColor: getColorStyle(color) }}></span></td>
                                    </tr>
                                ))}
                                {["Biały", "Czarny"].map(color => (
                                    <tr key={`sliski-${color}`}>
                                        <td>{color}</td>
                                        <td>Śliski</td>
                                        <td><span className="color-dot" style={{ backgroundColor: getColorStyle(`Śliski ${color}`) }}></span></td>
                                    </tr>
                                ))}
                            </tbody>
                        </table>
                    </div>
                </div>
                <div className="col-lg-6 mb-4">
                    <h3 className="mb-4"><FontAwesomeIcon icon={faQuestionCircle} className="me-2 text-primary"/> Najczęściej Zadawane Pytania</h3>
                    <div className="accordion accordion-flush" id="faqAccordionMinimal">
                        {faqData.map((item, index) => (
                            <div className="accordion-item minimal-accordion-item" key={index}>
                                <h2 className="accordion-header" id={`heading${index}`}>
                                    <button className="accordion-button collapsed minimal-accordion-button" type="button" data-bs-toggle="collapse" data-bs-target={`#collapse${index}`} aria-expanded="false" aria-controls={`collapse${index}`}>
                                        {item.question}
                                    </button>
                                </h2>
                                <div id={`collapse${index}`} className="accordion-collapse collapse" aria-labelledby={`heading${index}`} data-bs-parent="#faqAccordionMinimal">
                                    <div className="accordion-body minimal-accordion-body" dangerouslySetInnerHTML={{ __html: item.answer }}></div>
                                </div>
                            </div>
                        ))}
                    </div>
                </div>
            </div>
        </div>
    );
};

export default InfoFaqPage;